@use 'utils';

.cover {
  width: 100%;
  max-width: 600px;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 0 0px 0 rgba(blue, 0.5);
  @include utils.padding-hack(41.5%, cover);
  @include utils.transition(box-shadow);
  &:hover {
    box-shadow: 0 0 30px 0 rgba(blue, 0.5);
  }
}
